import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { VendorContainerComponent } from './vendor-container/vendor-container.component';
import { AdminGridContainerComponent } from '../admin-grid-container/admin-grid-container.component';
import { AdminTreetableContainerComponent } from '../admin-treetable-container/admin-treetable-container.component';
import { createGuardedGridRoute } from '../../../../shared/utils';
import { clientsGuard } from '../../guards/clients.guard';

const routes: Routes = [
  {
    path: '',
    component: VendorContainerComponent,
    children: [
      {
        path: '',
        redirectTo: 'details',
        pathMatch: 'full',
      },
      {
        path: 'accounts',
        children: [
          {
            path: '',
            redirectTo: 'vendor-users',
            pathMatch: 'full',
          },
          {
            path: ':type',
            component: AdminGridContainerComponent,
          },
          {
            path: ':type/:entityType/:entityId',
            component: AdminGridContainerComponent,
          },
        ],
      },
      {
        path: 'details',
        loadChildren: () =>
          import('./vendor-details/vendor-details-routing.module').then(
            (m) => m.VendorDetailsRoutingModule,
          ),
        data: { nestedContentScrollable: true },
      },
      {
        path: 'hardware',
        children: [
          {
            path: '',
            redirectTo: 'vendor-hardware-devices',
            pathMatch: 'full',
          },
          {
            path: ':type',
            component: AdminGridContainerComponent,
          },
          {
            path: ':type/:entityType/:entityId',
            component: AdminGridContainerComponent,
          },
        ],
      },
      {
        path: 'organization',
        children: [
          {
            path: '',
            redirectTo: 'vendor-cost-centres',
            pathMatch: 'full',
          },
          {
            path: ':type',
            component: AdminTreetableContainerComponent,
          },
          {
            path: ':type/:entityType/:entityId',
            component: AdminTreetableContainerComponent,
          },
        ],
      },
      {
        path: 'profiles',
        children: [
          {
            path: '',
            redirectTo: 'tree/vendor-company-groups',
            pathMatch: 'full',
          },
          {
            path: 'tree',
            children: [
              {
                path: ':type',
                component: AdminTreetableContainerComponent,
              },
              {
                path: ':type/:entityType/:entityId',
                component: AdminTreetableContainerComponent,
              },
            ],
          },
          {
            path: 'grid',
            children: [
              {
                path: ':type',
                component: AdminGridContainerComponent,
              },
              {
                path: ':type/:entityType/:entityId',
                component: AdminGridContainerComponent,
              },
            ],
          },
        ],
      },
      {
        path: 'providers',
        children: [
          {
            path: '',
            redirectTo: 'vendor-email-providers',
            pathMatch: 'full',
          },
          {
            path: ':type',
            component: AdminGridContainerComponent,
          },
          {
            path: ':type/:entityType/:entityId',
            component: AdminGridContainerComponent,
          },
        ],
      },
      ...createGuardedGridRoute(
        'clients',
        clientsGuard,
        AdminGridContainerComponent,
      ),
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class VendorsRoutingModule {}
